.forgot-password {
    &__content {
        max-width: rem(630px);
        margin: 0 auto;
        padding: rem(0 15px);
        min-height: rem(350px);
        display: flex;
        flex-direction: column;
        align-items: center;
        @include sm {
            min-height: auto;
            margin-bottom: rem(20px); } }
    &__title {
        margin-bottom: rem(95px);
        @include sm {
            margin-bottom: rem(30px); } }
    &__form {
        margin-bottom: rem(65px);
        @include sm {
            margin-bottom: rem(45px); } }
    &__form-group {
        margin-bottom: rem(73px);
        @include sm {
            margin-bottom: rem(30px); } }
    &__submit {
        text-align: center; }
    &__text {
        max-width: rem(485px);
        font-size: rem(22px);
        line-height: 1.45;
        @include sm {
            font-size: rem(16px);
            max-width: rem(350px); } } }
