//Colors
$blue-chat: #0066AA;
// $gray-chat: #F0F0FB
$gray-text: #9095A4;
$red-bright: #FF0000;
$dark-gray: #777777;

body {
    position: relative; }


.btn-color-white {
    color: $white; }
.btn {
    &:focus {
        outline: none;
        box-shadow: none; }
    &_upper {
        text-transform: uppercase; }
    &_bold {
        font-weight: 700; }
    &_xxl {
        font-size: rem(21px);
        @include sm {
            font-size: rem(18px); } }
    &_xl {
        font-size: rem(20px);
        @include r(1439) {
            font-size: rem(16px); } }
    &_m {
        font-size: rem(18px); }
    &_xs {
        font-size: rem(14px); }
    &_popup {
        font-size: rem(19px);
        line-height: 1;
        padding: rem(12px 35px);
        @include sm {
            font-size: rem(16px);
            padding: rem(10px 25px); } }
    &_sm {
        padding: rem(6px 40px);
        line-height: 1; } }


.width173 {
    width: rem(173px);
    @include r(1439) {
        width: rem(120px); } }
.width192 {
    width: rem(192px); }
.width197 {
    width: rem(197px); }
.width212 {
        width: rem(212px); }
.width270 {
    width: rem(270px); }
.width309 {
    width: rem(309px);
    @include sm {
        width: rem(250px); } }

.branded-list {
    &__item {
        position: relative;
        padding-left: rem(36px);
        font-weight: 300;
        font-size: rem(23px);
        @include r(1439) {
            font-size: rem(18px); }
        @include sm {
            font-size: rem(14px); }
        &:not(:last-child) {
            margin-bottom: rem(29px);
            @include sm {
                margin-bottom: rem(15px); } }
        span {
            position: absolute;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            display: block;
            width: rem(17px);
            height: rem(17px);
            overflow: hidden;
            background-color: #fff;
            border-radius: 50%;
            font-size: 0;
            @include sm {
                top: rem(1px);
                transform: none; } }
        .icon-icon {
            font-size: rem(20px);
            position: relative;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%); } } }

.social {
    display: flex;
    &__item {
        width: rem(36px);
        height: rem(36px);
        border-radius: 50%;
        background-color: $primary;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: rem(19px);
        @include r(1439) {
            width: rem(22px);
            height: rem(22px);
            font-size: rem(14px); }
        &:not(:last-child) {
            margin-right: rem(22px);
            @include r(1439) {
                margin-right: rem(15px); } }
        .icon {
            color: $gray-900; } } }


.input-dark {
    background-color: transparent;
    border: none;
    border-bottom: 1px solid $white;
    border-radius: 0;
    resize: none;
    color: $white;
    font-size: rem(18px);
    padding: rem(13px 0);
    @include placeholder {
        color: $white; }
    &:-webkit-autofill,
    &:-webkit-autofill:hover,
    &:-webkit-autofill:focus {}
    &:-webkit-autofill {
        -webkit-text-fill-color: $white !important;
        background-color: transparent !important;
        box-shadow: inset 0 0 0 1000px $gray-900 !important; }
    &:focus {
        background-color: transparent;
        color: $white;
        &::placeholder {
            font-size: 0; } } }

.md-hide {
    @include md {
        display: none; } }

.md-show {
    @include md {
        display: block; } }
