/*Sandwich*/
.toggle-mnu {
  position: relative;
  float: right;
  width: 2rem;
  height: 2rem;
  display: none;
  z-index: 100;
  border: none;
  background-color: transparent;
}

@media only screen and (max-width: 1023px) {
  .toggle-mnu {
    display: block;
  }
}

.toggle-mnu:before {
  content: "";
  position: absolute;
  top: -0.625rem;
  left: -0.625rem;
  right: -0.625rem;
  bottom: -0.625rem;
}

.toggle-mnu:focus {
  outline: none;
}

.sandwich {
  width: 2rem;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 200;
  top: 50%;
  transform: translateY(-50%);
}

.sw-topper {
  display: block;
  position: relative;
  transform-origin: left top;
  width: 2rem;
  height: 0.1875rem;
  background: white;
  border: none;
  border-radius: 0.25rem 0.25rem 0.25rem 0.25rem;
  transition: transform 0.5s;
  margin-bottom: 0.3125rem;
}

.sw-bottom {
  overflow: hidden;
  display: block;
  position: relative;
}

.sw-bottom__inner {
  display: block;
  position: relative;
  width: 2rem;
  height: 0.1875rem;
  transform-origin: center center;
  background: white;
  border: none;
  border-radius: 0.25rem 0.25rem 0.25rem 0.25rem;
  transition: transform 0.3s ease-in-out, opacity .5s ease-in-out;
  margin-bottom: 0.3125rem;
}

.sw-footer {
  display: block;
  position: relative;
  width: 2rem;
  height: 0.1875rem;
  transform-origin: left;
  background: white;
  border: none;
  border-radius: 0.25rem 0.25rem 0.25rem 0.25rem;
  transition: transform 0.5s;
}

.sandwich.is-active .sw-topper {
  transform: translate(0, -3px) rotate(45deg);
}

.sandwich.is-active .sw-bottom__inner {
  opacity: 0;
  transform: translateX(-105%);
}

.sandwich.is-active .sw-footer {
  transform: translate(0, 3px) rotate(-45deg);
}

@keyframes moveIn {
  0% {
    transform: translateX(-102%);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes moveOut {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(102%);
  }
}

