.slide {
    width: 100%;
    height: 100vh;
    overflow: auto;
    @include vertical;
    @include md {
        height: auto;
        padding: rem(15px 0); }
    &__text {
        position: relative;
        z-index: 1; }
    &__inner {
        padding-top: rem(126px);
        display: inline-block;
        vertical-align: middle;
        text-align: left;
        font-size: 1rem;
        @include sm {
            padding-top: rem(60px); } }
    &__img {
        img {
            display: block;
            max-height: calc(100vh - 258px);
            min-height: rem(400px);
            width: auto;
            max-width: 100%;
            @include r(1439) {
                max-height: calc(100vh - 233px);
                min-height: rem(300px); }
            @include sm {
                min-height: rem(100px); } } }
    &__footer {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%; } }

.marketplace {
    &__inner {}
    &__content {
        display: flex;
        align-items: center;
        justify-content: space-between;
        @include md {
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start; } }
    &__text {
        max-width: rem(705px);
        font-size: rem(20px);
        @include r(1439) {
            font-size: rem(16px);
            max-width: rem(550px); }
        @include md {
            margin-bottom: rem(30px);
            width: 100%;
            max-width: none; }
        @include sm {
            font-size: rem(14px); } }
    &__title {
        font-size: rem(55px);
        font-weight: 700;
        font-family: $font-family-montserrat;
        margin-bottom: rem(30px);
        @include r(1439) {
            font-size: rem(40px); }
        @include sm {
            font-size: rem(30px); }
        span {
            color: $primary; } }
    &__btns {
        margin-top: rem(42px);
        @include md {
            display: flex;
            flex-direction: column;
            align-items: center; }
        .btn-primary {
            margin-right: rem(60px);
            @include md {
                margin-right: 0;
                margin-bottom: rem(25px); } } }

    &__img {
        margin-left: rem(-460px);
        padding-right: rem(60px);
        @include r(1439) {
            padding-right: 0; }
        @include md {
            margin-left: 0;
            padding-right: 0; } } }


