.join {
    background-repeat: no-repeat;
    background-position: rem(960px 41px);
    &__inner {}
    &__content {
        display: flex;
        align-items: center;
        @include md {
            flex-direction: column;
            align-items: flex-start; } }
    &__img {
        @include md {
            order: 2; } }
    &__text {
        max-width: rem(885px);
        margin-left: rem(-230px);
        font-size: rem(24px);
        font-family: $font-family-montserrat;
        @include r(1439) {
            font-size: rem(18px);
            max-width: rem(635px); }
        @include md {
            margin-left: 0;
            margin-bottom: rem(30px);
            width: 100%;
            max-width: none; }
        @include sm {
            font-size: rem(14px); } }
    &__title {
        font-size: rem(55px);
        font-weight: 700;
        color: $primary;
        margin-bottom: rem(30px);
        @include r(1439) {
            font-size: rem(40px); }
        @include md {
            font-size: rem(30px); } }
    &__wrap {
        padding-left: rem(90px);
        @include sm {
            padding-left: rem(50px); } }
    &__advantages {
        position: relative;
        font-size: rem(30px);
        margin-bottom: rem(20px);
        font-weight: 700;
        @include r(1439) {
            font-size: rem(24px); }
        @include sm {
            font-size: rem(18px); }
        &:before {
            content: "";
            position: absolute;
            width: rem(123px);
            height: rem(2px);
            background-color: $primary;
            left: rem(-150px);
            top: rem(17px);
            @include r(1439) {
                top: rem(13px); }
            @include md {
                width: rem(70px);
                left: rem(-90px); }
            @include sm {
                width: rem(30px);
                left: rem(-50px);
                top: rem(9px); } } } }
