//FONTS
@mixin fontpath($name, $name1, $file) {
	src: local($name), local($name1),
	url("../fonts/" + $file + ".woff") format("woff"),
	url("../fonts/" + $file + ".woff2") format("woff2") {} }
@mixin fontdefault($style, $weight, $name) {
	font-family: $name;
	font-style: $style;
	font-weight: $weight; }

//Start Inconsolata
@font-face {
	@include fontdefault(normal, 400, 'Inconsolata');
	@include fontpath('Inconsolata', 'InconsolataLGC', 'InconsolataLGC'); }
@font-face {
	@include fontdefault(normal, 700, 'Inconsolata');
	@include fontpath('Inconsolata Bold', 'InconsolataLGC-Bold', 'InconsolataLGC-Bold'); }
@font-face {
	@include fontdefault(italic, 700, 'Inconsolata');
	@include fontpath('Inconsolata BoldItalic', 'InconsolataLGC-BoldItalic', 'InconsolataLGC-BoldItalic'); }
@font-face {
	@include fontdefault(italic, 400, 'Inconsolata');
	@include fontpath('Inconsolata Italic', 'InconsolataLGC-Italic', 'InconsolataLGC-Italic'); }

