.interest {
    &__inner {}
    &__content {
        display: flex;
        align-items: center;
        @include r(1439);
        @include sm {
            flex-direction: column;
            align-items: flex-start; } }
    &__text {
        font-size: rem(24px);
        font-family: $font-family-montserrat;
        max-width: rem(755px);
        flex-basis: 50%;
        margin-right: rem(168px);
        padding-left: rem(35px);
        @include r(1439) {
            font-size: rem(18px);
            margin-right: rem(20px); }
        @include sm {
            padding-left: 0;
            margin-right: 0;
            font-size: rem(14px);
            // max-width: none
            // display: flex
            // justify-content: space-between
            // padding-left: 0
 } }            // margin-right: 0
    &__wrap {
        margin-bottom: rem(50px);
        @include r(1439) {
            max-width: rem(500px); }
        @include sm {
            max-width: none;
            margin-bottom: rem(25px); }
        p {
            max-width: rem(660px);
            @include sm {
                max-width: none; } } }
    &__title {
        font-size: rem(30px);
        font-weight: 700;
        color: $primary;
        margin-bottom: rem(25px);
        @include r(1439) {
            font-size: rem(24px); }
        @include sm {
            font-size: rem(18px);
            margin-bottom: rem(15px); }
        &_lg {
            font-size: rem(34px);
            @include r(1439) {
                font-size: rem(28px); }
            @include sm {
                font-size: rem(20px); } } }
    &__img {
        flex-basis: 50%;
        @include r(1439) {
            padding-right: rem(50px); }
        @include sm {
            padding-right: 0; }
        img {
            max-width: 570px;
            @include r(1439) {
                max-width: 100%; } } } }
