.why-join {
    background-repeat: no-repeat;
    background-position: 0 0;
    background-size: contain;
    @include sm {
        background-size: cover; }
    &__inner {}
    &__content {
        padding-left: rem(50px);
        @include sm {
            padding-left: 0; } }
    &__title {
        font-size: rem(74px);
        font-weight: 700;
        color: $primary;
        margin-bottom: rem(20px);
        padding-left: rem(104px);
        @include r(1439) {
            font-size: rem(50px); }
        @include sm {
            font-size: rem(30px);
            padding-left: 0; } }
    &__text {
        padding-left: rem(159px);
        font-family: $font-family-montserrat;
        margin-bottom: rem(115px);
        @include r(1439) {
            margin-bottom: rem(50px); }
        @include sm {
            padding-left: rem(50px);
            margin-bottom: rem(30px); } }
    &__advantages {
        position: relative;
        font-size: rem(28px);
        margin-bottom: rem(45px);
        font-weight: 600;
        @include r(1439) {
            font-size: rem(20px); }
        @include sm {
            font-size: rem(14px);
            margin-bottom: rem(25px); }
        &:before {
            content: "";
            position: absolute;
            width: rem(123px);
            height: rem(2px);
            background-color: $primary;
            left: rem(-159px);
            top: rem(8px);
            @include sm {
                width: rem(30px);
                left: rem(-50px);
                top: rem(5px); } } }
    &__list {}
    &__item {
        .icon {
            color: $gray-900; } }
    &__slogan {
        opacity: 0.7;
        font-weight: 700;
        font-size: 26px;
        @include sm {
            font-size: rem(18px); } } }

